.list {
	display: inline-block;
	vertical-align: top;
	@include xs {
		max-width: 400px; }
	&__item {
		@include md-min {
			max-width: 360px;
			&:not(:first-of-type) {
				margin-top: 40px; } }
		@include md {
			&:not(:first-of-type) {
				margin-top: 15px; } }
		@include xs-min {
			display: table; } }
	&__item-icon {
		display: table-cell;
		vertical-align: middle;
		@include xs {
			display: none; } }
	&__item-text {
		@include roboto-light;
		font-size: 22px;
		line-height: 1.2;
		text-transform: lowercase;
		@include md-min {
			padding-left: 20px; }
		@include md {
			font-size: 18px; }
		@include xs-min {
			display: table-cell;
			vertical-align: middle;
			padding-left: 15px; } } }
