%inner {
	max-width: 1880px;
	margin: auto;
	padding: 0 15px; }
%button {
	display: inline-block;
	vertical-align: top;
	position: relative;
	border-radius: 3px;
	text-transform: uppercase;
	transition: background-color 0.3s;
	text-align: center; }
%button-basic {
	display: inline-block;
	vertical-align: top;
	position: relative;
	text-align: center;
	cursor: pointer; }
%img {
	max-width: 100%;
	max-height: 100%;
	display: block; }
%popup {
	position: fixed;
	padding: 60px;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 300;
	overflow-x: hidden;
	overflow-y: auto;
	visibility: hidden;
	opacity: 0;
	transition: all 0.3s;
	background-color: $violet;
	text-align: center;
	font-size: 0; }
%parallax {
	position: absolute;
	top: 110px;
	right: 0;
	bottom: 0;
	left: 0;
	overflow: hidden;
	perspective: 800px; }
%parallax-item {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	overflow: hidden; }
