.tabs {
	text-align: left;
	@include clr; }
.tabs-content {
	position: relative;
	@include sm-min {
		float: right;
		width: 594px;
		padding-left: 30px; }
	@include sm {
		margin-left: -20px; }
	@include xs {
		margin-left: 0; }
	&__item {
		display: none;
		&.is-active {
			display: block; } } }
.tabs-control {
	@include sm-min {
		float: left;
		width: 142px;
		min-height: 1px; }
	@include sm {
		margin-left: -15px; }
	&__link {
		font-size: 24px;
		line-height: 1.2;
		display: block;
		padding-bottom: 36px;
		color: rgba(#fff, 0.4);
		transition: color 0.3s;
		position: relative;
		cursor: default;
		@include sm {
			width: 33.3333%;
			display: inline-block;
			vertical-align: top;
			padding-left: 15px;
			font-size: 18px;
			padding-bottom: 20px; }
		&:not(:first-of-type) {
			@include sm-min {
				margin-top: 20px; } }
		&:after {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			height: 4px;
			transform: scaleY(1);
			transition: transform 0.3s, background-color 0.3s;
			background-color: rgba(#fff, 0.4);
			@include sm {
				left: 15px; } }
		&.is-active {
			color: #fff;
			&:after {
				transform: scaleY(2);
				background-color: #fff;
				@include sm {
					transform: scaleY(1.5); } } } } }
