.preloader {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	width: 100%;
	height: 100%;
	background-color: $violet; 
	
}
.preloader__title {
	position: absolute;
	@include panton;
	font-size: 16px;;
	text-transform: uppercase;
	left: 50%;
	top: 58%;
	transform: translateX(-50%);
	z-index: 100;
	opacity: 1;
	transition: 0.5s opacity ease-in-out;
}
.preloader__title.is-hidden {
	opacity: 0;
}

.preloader__body {
	position: absolute;
	top: 50%;
	margin-left: -50px;
	left: 50%;
	animation: speeder .4s linear infinite;
}

.preloader__body > span {
	height: 5px;
	width: 35px;
	background: #fff;
	position: absolute;
	top: -19px;
	left: 60px;
	border-radius: 2px 10px 1px 0;
}

.preloader__base span {
	position: absolute;
	width: 0;
	height: 0;
	border-top: 6px solid transparent;
	border-right: 100px solid #fff;
	border-bottom: 6px solid transparent;
}

.preloader__base span:after {
	content: "";
	height: 22px;
	width: 22px;
	border-radius: 50%;
	background: #fff;
	position: absolute;
	right: -110px;
	top: -16px;
}

.preloader__base span:before {
	content: "";
	position: absolute;
	width: 0;
	height: 0;
	border-top: 0 solid transparent;
	border-right: 55px solid #fff;
	border-bottom: 16px solid transparent;
	top: -16px;
	right: -98px;
}

.preloader__face {
	position: absolute;
	height: 12px;
	width: 20px;
	background: #fff;
	border-radius: 20px 20px 0 0;
	transform: rotate(-40deg);
	right: -125px;
	top: -15px;
}

.preloader__face:after {
	content: "";
	height: 12px;
	width: 12px;
	background: #fff;
	right: 4px;
	top: 7px;
	position: absolute;
	transform: rotate(40deg);
	transform-origin: 50% 50%;
	border-radius: 0 0 0 2px;
}

.preloader__body > span > span:nth-child(1),
.preloader__body > span > span:nth-child(2),
.preloader__body > span > span:nth-child(3),
.preloader__body > span > span:nth-child(4){
	width: 30px;
	height: 1px;
	background: #fff;
	position: absolute;
	animation: fazer1 .2s linear infinite;
}

.preloader__body > span > span:nth-child(2) {
	top: 3px;
	animation: fazer2 .4s linear infinite;
}

.preloader__body > span > span:nth-child(3) {
	top: 1px;
	animation: fazer3 .4s linear infinite;
	animation-delay: -1s;
}

.preloader__body > span > span:nth-child(4) {
	top: 4px;
	animation: fazer4 1s linear infinite;
	animation-delay: -1s;
}

@keyframes fazer1 {0% {left: 0;} 100% {left: -80px;opacity: 0;}}

@keyframes fazer2 {0% {left: 0;} 100% {left: -100px;opacity: 0;}}

@keyframes fazer3 {0% {left: 0;} 100% {left: -50px;opacity: 0;}}

@keyframes fazer4 {0% {left: 0;} 100% {left: -150px;opacity: 0;}}

@keyframes speeder {
	0% { transform: translate(2px, 1px) rotate(0deg); }
	10% { transform: translate(-1px, -3px) rotate(-1deg); }
	20% { transform: translate(-2px, 0px) rotate(1deg); }
	30% { transform: translate(1px, 2px) rotate(0deg); }
	40% { transform: translate(1px, -1px) rotate(1deg); }
	50% { transform: translate(-1px, 3px) rotate(-1deg); }
	60% { transform: translate(-1px, 1px) rotate(0deg); }
	70% { transform: translate(3px, 1px) rotate(-1deg); }
	80% { transform: translate(-2px, -1px) rotate(1deg); }
	90% { transform: translate(2px, 1px) rotate(0deg); }
	100% { transform: translate(1px, -2px) rotate(-1deg); }
}

.preloader__longfazers {
  position: absolute;
  width: 100%;
  height: 100%;
}

.preloader__longfazers span {
  position: absolute;
  height: 2px;
  width: 20%;
  background: #fff;
}

.preloader__longfazers span:nth-child(1) {
  top: 20%;
  animation: lf .6s linear infinite;
  animation-delay: -5s;
}

.preloader__longfazers span:nth-child(2) {
  top: 40%;
  animation: lf2 .8s linear infinite; 
  animation-delay: -1s;
}

.preloader__longfazers span:nth-child(3) {
  top: 60%;
  animation: lf3 .6s linear infinite; 
}

.preloader__longfazers span:nth-child(4) {
  top: 80%;
  animation: lf4 .5s linear infinite; 
  animation-delay: -3s;
}

@keyframes lf {0% {left: 200%;} 100% {left: -200%;opacity: 0;}}
@keyframes lf2 {0% {left: 200%;} 100% {left: -200%;opacity: 0;}}
@keyframes lf3 {0% {left: 200%;} 100% {left: -100%;opacity: 0;}}
@keyframes lf4 {0% {left: 200%;} 100% {left: -100%;opacity: 0;}}
