.out {
	min-width: 320px;
	height: 100%;
	overflow: hidden;
	.ps__scrollbar-y-rail {
		top: 0 !important;
		bottom: 0 !important; }
	.ps__scrollbar-x-rail {
		display: none !important; } }
.inner {
	@extend %inner;
	position: relative;
	@include clr;
	@include sm {
		padding: 0 10px; } }
.perfomance {
	.inner {
		height: 100vh;
		min-height: 450px;
		@include vertical; }
	&__button {
		display: inline-block;
		vertical-align: middle;
		position: relative;
		z-index: 10;
		padding-top: 260px;
		@include lg {
			padding-top: 290px; }
		@include sm {
			padding-top: 180px; } } }
.work {
	.inner {
		padding-top: 80px;
		padding-bottom: 80px; } }
.web {
	position: relative;
	text-align: center;
	@include md-min {
		&:before {
			content: '';
			display: block;
			position: absolute;
			background: url(../img/lines-bg.png) no-repeat center;
			background-size: cover;
			top: 0;
			right: 0;
			left: 38px;
			height: 1218px;
			z-index: 2; } }
	.inner {
		z-index: 2;
		@include md-min {
			padding-top: 120px;
			@include css-lock(200, 450, 600, 1920, 'padding-bottom'); }
		@include md {
			padding-top: 50px;
			padding-bottom: 50px; } } }
.recl {
	position: relative;
	@include md-min {
		&:before {
			content: '';
			display: block;
			position: absolute;
			background: url(../img/lines-bg.png) no-repeat center;
			background-size: cover;
			top: 0;
			right: 0;
			left: 38px;
			height: 1218px;
			z-index: 1; } }
	.inner {
		z-index: 4;
		@include md-min {
			padding-top: 120px;
			@include css-lock(200, 450, 900, 1920, 'padding-bottom'); }
		@include md {
			padding-top: 50px;
			padding-bottom: 50px; } } }
.top-offers {
	.inner {
		padding-top: 110px;
		padding-bottom: 110px;
		@include md {
			padding-top: 50px;
			padding-bottom: 50px; } } }
.description {
	text-align: center;
	.inner {
		padding-top: 86px;
		padding-bottom: 97px;
		@include md {
			min-height: auto;
			padding-top: 50px;
			padding-bottom: 50px; } } }
