.page {
	padding: 100px;
	position: relative;
	min-height: 100%;
	background-color: $violet;
	@include md {
		padding: 60px 20px; }
	@include xs {
		padding: 100px 10px; }
	&__content {
		max-width: 600px;
		width: 100%;
		margin: auto; }
	&__title {
		font-size: 48px;
		@include roboto-reg;
		@include sm {
			font-size: 26px; } }
	&__sub {
		padding-top: 8px; }
	&__body {
		padding-top: 20px;
		@include sm-min {
			padding-top: 40px;
			padding-left: 26px; }
		@include sm {
			@include xs-min {
				margin-left: -20px; } } }
	.form__row {
		width: 100%;
		&:before {
			display: none; } } }
