.company {
	position: relative;
	text-align: center;
	@include vcenter;
	@include panton-bold;
	max-width: 700px;
	min-width: 340px;
	margin-top: -((100px / 1880px) * 100%);
	@include sm {
		margin-top: -100px; }
	@include xs {
		min-width: 300px; }
	&__pretitle {
		@include css-lock(28, 36, 900, 1920);
		letter-spacing: 0.2em;
		color: #fff;
		font-family: Arial, Helvetica, sans-serif;
		@include sm {
			font-size: 24px; }
		@include xs {
			font-size: 20px; } }
	&__title {
		max-width: 530px;
		width: 100%;
		padding-top: 15px;
		margin: auto;
		@include hide-text;
		svg {
			@extend %img; } } }
